$cellSize: 32px;

.cell {
  display: inline-block;
  width: $cellSize;
  height: $cellSize;
  float: left;
  border-top: var(--cellBorder);
  border-left: var(--cellBorder);
  background-color: darkgrey;
  font: inherit;
  font-size: 18px;
  font-weight: bold;
  line-height: $cellSize;
  text-align: center;
}

.visible {
  background-color: lightgrey;
}

.busted {
  background-color: red;
}

.wrong {
  position: relative;

  &::before,
  &::after {
    content: '';
    width: 2px;
    height: 100%;
    background: red;
    position: absolute;
    top: 0;
    left: 45%;
  }

  &::before {
    transform: rotate(-45deg);
  }

  &::after {
    transform: rotate(45deg);
  }
}

.value1 {
  color: blue;
}

.value2 {
  color: green;
}

.value3 {
  color: red;
}

.value4 {
  color: purple;
}

.value5 {
  color: maroon;
}

.value6 {
  color: turquoise;
}

.value7 {
  color: black;
}

.value8 {
  color: darkgrey;
}

:export {
  cellSize: $cellSize;
}
